.theme-light {
  background-color: #ffffff;
  color: #000000;
  header {
    background-color: #285577;
    color: #ffffff;
    border: 2px solid #4c7899;
    padding: 7px 0px 5px 5px;
  }
  nav {
    background-color: #bfc7c9;
    color: #000000;
    span:hover {
      background-color: #a7afb1;
    }
  }
  main {
    padding-left: 5px;
    padding-top: 1px;
  }
  aside {
    padding: 3px 2px 2px 2px;
    background-color: #f0f0f0;
  }
  hr {
    border-top: 2px solid #bbb;
    margin: 16px 0px 16px 0px;
  }
  footer {
    background-color: #4c7899;
    color: #ffffff;
    padding: 6px 5px 5px 4.7px;
  }
  a {
    color: #0000ff;
    &:visited {
      color: #793db0;
    }
    &:active {
      color: #ff0000;
    }
  }
  .project-card {
    color: #000000;
    background-color: #ffffff;
    &:visited {
      color: #000000;
    }
    section:hover {
      background-color: #dddddd;
    }
  }
  .header-wrapper {
    footer {
      background-color: #4c7899aa;
    }
    header {
      background-color: #285577aa;
      border: 2px solid #4c7899aa;
    }
    nav, .pulltab {
      background-color: #bfc7c9aa;
    }
  }
}

.theme-dark {
  background-color: #111111;
  color: #d5d4d3;
  header {
    color: #55ff55;
    border-top: 1px solid #55ff55;
    border-bottom: 1px solid #55ff55;
    padding: 8px 2px 6px 7px;
  }
  nav {
    border-bottom: 1px solid #55ff55;
    span:hover {
      background-color: #ff00ff;
      color: #000000;
    }
  }
  main {
    padding-left: 5px;
  }
  aside {
    border-right: 1px solid #55ff55;
    padding: 2px;
  }
  hr {
    border-top: 1px solid #090;
    margin: 16px 0px 17px 0px;
  }
  footer {
    color: #00ffff;
    border-top: 1px solid #55ff55;
    padding: 5px 5px 5px 4.7px;
  }
  img {
    margin-top: 1px;
    margin-left: 1px;
  }
  a {
    color: #1111ff;
    &:visited {
      color: #9b5cd2;
    }
    &:active {
      color: #ff0000;
    }
  }
  .project-card {
    color: #d5d4d3;
    background-color: #111111;
    &:visited {
      color: #d5d4d3;
    }
    section:hover {
      background-color: #dddddd;
      color: #000000;
    }
  }
  .header-wrapper {
    footer, header, nav, .pulltab {
      background-color: #11111166;
    }
    .pulltab {
      margin-top: -1px;
      padding-top: -2px;
      border: 1px solid #55ff55;
    }
  }
}
