*/

* {
  box-sizing: border-box;
}

html {
  height: 100%;
  width: 100%;
}

body {
  display: flex;
  flex-direction: column;
  filter: contrast(1);
  margin: 0px;
  min-height: 100%;
  width: 100%;
  font-family: Alegreya;
}

.toggle-wrapper {
  box-sizing: content-box;
  float: right;
  transform: scale(40%) translate(60%, -60%);
  * {
    box-sizing: content-box;
  }
}

header {
  font-family: "Terminus";
  font-weight: bold;
  width: 100%;
  flex-grow: 0;
}

nav {
  font-family: Fixedsys, monospace;
  padding: 0px 0px 2px 2px;
  width: 100%;
  flex-grow: 0;
  span {
    padding: 0px 2px 2px 2px;
  }
}

.contentwrapper {
  margin: 0px;
  padding: 0px;
  display: flex;
  flex-grow: 1;
  height: 100%;
}


aside,
article,
main {
  h1, h2, h3, h4, h5, h6 {
    font-family: Fixedsys, monospace;
  }
  h2 {
    padding-left: 0.5px;
  }
  h4 {
    padding-left: 1px;
  }
  h6 {
    padding-left: 0.3px;
  }
}

aside {
  width: 200px;
}

main {
  flex: 1;
  padding-right: 10px;
}

article {
  padding: 2px;
}

section {
  padding: 2px;
}

hr {
  display: block;
  height: 1px;
  border: 0;
  padding: 0;
}

footer {
  width: 100%;
  font-family: Terminus;
  flex-grow: 0;
}

.project-card {
  text-decoration: none;
  h1 {
    margin-top: 10px;
    margin-bottom: 10px;
  }
  img {
    margin: 0px 10px 0px 10px;
  }
  div {
    display: flex;
    margin-bottom: 10px;
    div {
      display: block
    }
  }
}

.header-wrapper {
  position: absolute;
  width: 100%;
  z-index: 10;
  transition-duration: 300ms;
  transform: translateY(-97%);
  &:hover {
    transform: translateY(0%);
  }
  .pulltab {
    font-family: Fixedsys, monospace;
    text-align: center;
    font-size: 30px;
    position: absolute;
    padding: 0em 0.03em 1em 0em;
    border-radius: 0% 0% 30% 30%;
    border-top: 0px;
    width: 30px;
    height: 20px;
    left: 50%;
    top: 100%;
  }
}

